
body
        {
        font-family: 'Times New Roman', Times, serif;
        font-size: 16px;
        margin:0;
        background: #072e4c;
        color:#000;
        display:flex;  
        min-height:100vh;
        justify-content: space-around;  
        align-items: center;
        }
.c
{
  text-align: center;
  color: white;
  margin-top:34px;
  padding-bottom:20px;
  font-size: xx-large;
  font-family: 'Times New Roman', Times, serif;
}
 
textarea {

  width:330px;
  height:200px;
  background:linear-gradient(to bottom, #007193 50%,#06024e);
  border-radius: 40px;
  font-size: x-large;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 20px;
  padding-bottom: 20px;
}
.wc {
  color:white;
  font-weight: bold;
  text-align: center;
  font-size: xx-large;
  padding-bottom:50px;
  font-family: 'Times New Roman', Times, serif;
}

